* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
	font-family: 'Raleway', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;	
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
	color: #ccc;
	font-size: 16px;
	line-height: normal;
	vertical-align: middle;
	font-weight: 600;
	letter-spacing: 0;
}

/*

Loading animation
*/

svg {
	-webkit-animation: rotate 1s linear infinite;
	        animation: rotate 1s linear infinite;
	-webkit-animation-direction: normal;
	        animation-direction: normal;
}

.load {
	fill: none;
	stroke: tomato;
	stroke-width: 3;
	stroke-dasharray: 150;
	stroke-dashoffset: 120;
	-webkit-animation: loader 1.2s ease infinite;
	        animation: loader 1.2s ease infinite;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
}

@-webkit-keyframes loader {
	30% {
		stroke: #58cc5c;
	}
	100% {
		stroke-dashoffset: 0;
		stroke: #03A9F4;
	}
}

@keyframes loader {
	30% {
		stroke: #58cc5c;
	}
	100% {
		stroke-dashoffset: 0;
		stroke: #03A9F4;
	}
}

@-webkit-keyframes rotate {
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes rotate {
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}


/*

General styles
*/

.container {
	position: relative;
	max-width: 860px;
	padding: 0 20px;
	margin: auto;
}

.poke-container {
	padding: 30px 0 70px 0;
}

.poke__header {
	position: relative;
	padding: 80px 0;
	background: #03A9F4;
	text-align: center;
	-webkit-box-shadow: inset 0px -30px 40px -30px rgba(0, 0, 0, 0.21);
	        box-shadow: inset 0px -30px 40px -30px rgba(0, 0, 0, 0.21);
}

.poke__header::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-image: url(https://pro-rankedboost.netdna-ssl.com/wp-content/uploads/2017/01/Pokemon-GO-Shiny-Pokemon-List.png);
	background-repeat: repeat;
	opacity: .07;
	-webkit-animation: moveBG 26s linear infinite;
	        animation: moveBG 26s linear infinite;
}

@-webkit-keyframes moveBG {
	from {
		background-position: center 0;
	}
	to {
		background-position: center 100%;
	}
}

@keyframes moveBG {
	from {
		background-position: center 0;
	}
	to {
		background-position: center 100%;
	}
}

.poke__title {
	margin: 0;
	font-size: 33px;
	color: #fff;
	font-weight: 600;
	text-shadow: 0px 2px 7px rgba(0, 0, 0, 0.2);
}

.poke__search-group {
	display: inline-block;
	max-width: 500px;
	width: 100%;
	margin: auto;
	position: relative;
}

.poke__search--field {
	width: 100%;
	margin-top: 20px;
	padding: 10px 15px;
	border: 1px solid #fff;
	border-radius: 4px;
	-webkit-box-shadow: 0px 2px 13px -5px rgba(0, 0, 0, 0.4);
	        box-shadow: 0px 2px 13px -5px rgba(0, 0, 0, 0.4);
	outline: none;
	font-size: 19px;
	line-height: normal;
	letter-spacing: .5px;
	color: dimgrey;
	-webkit-transition: all .2s ease;
	-o-transition: all .2s ease;
	transition: all .2s ease;
	font-weight: 600;
	font-family: 'Raleway', sans-serif;
}

.poke__search--field:focus {
	-webkit-box-shadow: 0px 2px 21px -5px rgba(0, 0, 0, 0.76);
	        box-shadow: 0px 2px 21px -5px rgba(0, 0, 0, 0.76);
	-webkit-transform: scale(1.2) translateY(6px);
	    -ms-transform: scale(1.2) translateY(6px);
	        transform: scale(1.2) translateY(6px);
}

.poke__search-load-wrapper {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	padding: 60px 10px;
}

.poke__load {
	width: 90px;
	height: 90px;
}

.poke__icon-box {
	position: relative;
	z-index: 1;
	width: 105px;
	height: 105px;	
	display: -ms-flexbox;	
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;	
	border-radius: 100%;
	border: 1px solid #fbfbfb;
	background-color: #fbfbfb;
	-webkit-box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.25);
	        box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.25);
}

.poke__icon-box::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border-radius: 100%;
	display: block;
	background: url(http://www.freeiconspng.com/uploads/pokeball-icon-4.png);
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: center;
	-webkit-animation: fadeOut .5s 2s ease forwards;
	        animation: fadeOut .5s 2s ease forwards;
}

@-webkit-keyframes fadeOut {
	100% {
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
		opacity: 0;
	}
}

@keyframes fadeOut {
	100% {
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
		opacity: 0;
	}
}

.poke__card-wrapper {
	margin-top: 30px;
	border-radius: 10px;
	background: #fff;
	display: -ms-flexbox;
	display: flex;
	min-height: 200px;
	-webkit-animation: pokeBoxAppear .4s ease forwards;
	        animation: pokeBoxAppear .4s ease forwards;
	will-change: transform;
}

@-webkit-keyframes pokeBoxAppear {
	0% {
		-webkit-transform: translate3d(0, 20px, 0) scale(.9);
		        transform: translate3d(0, 20px, 0) scale(.9);
		opacity: 0;
	}
	100% {
		-webkit-transform: translate3d(0, 0, 0) scale(1);
		        transform: translate3d(0, 0, 0) scale(1);
		opacity: 1;
	}
}

@keyframes pokeBoxAppear {
	0% {
		-webkit-transform: translate3d(0, 20px, 0) scale(.9);
		        transform: translate3d(0, 20px, 0) scale(.9);
		opacity: 0;
	}
	100% {
		-webkit-transform: translate3d(0, 0, 0) scale(1);
		        transform: translate3d(0, 0, 0) scale(1);
		opacity: 1;
	}
}

.poke__card-thumb,
.poke__card-info {
	min-height: 100%;
}

.poke__card-thumb {
	-webkit-transform: translateX(300px) scale(.95);
	    -ms-transform: translateX(300px) scale(.95);
	        transform: translateX(300px) scale(.95);
	-webkit-animation: thumbPokemonAppear .6s .7s ease forwards;
	        animation: thumbPokemonAppear .6s .7s ease forwards;
}

@-webkit-keyframes thumbPokemonAppear {
	100% {
		-webkit-transform: translateX(30px) scale(.95);
		        transform: translateX(30px) scale(.95);
	}
}

@keyframes thumbPokemonAppear {
	100% {
		-webkit-transform: translateX(30px) scale(.95);
		        transform: translateX(30px) scale(.95);
	}
}

.poke__card-info {
	-webkit-transform: translateX(-90px);
	    -ms-transform: translateX(-90px);
	        transform: translateX(-90px);
	-webkit-animation: thumbPokemonInfoAppear .6s .5s ease forwards;
	        animation: thumbPokemonInfoAppear .6s .5s ease forwards;    
}

@-webkit-keyframes thumbPokemonInfoAppear {
	100% {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
}

@keyframes thumbPokemonInfoAppear {
	100% {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
}

.poke__card-thumb {
	position: relative;
	z-index: 1;
	-ms-flex: 1 0 40%;
	flex: 1 0 40%;
	padding: 20px;
	background: url(https://vignette3.wikia.nocookie.net/logopedia/images/e/e5/Pokemon_logo.png/revision/latest?cb=20120128115827);
	background-size: 80px;
	background-position: center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	border-radius: 6px;
	overflow: hidden;
}

.poke__card-thumb::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 144, 210, 0.92)), to(rgba(88, 204, 92, 0.99)));
	background: -webkit-linear-gradient(rgba(0, 144, 210, 0.92), rgba(88, 204, 92, 0.99));
	background: -o-linear-gradient(rgba(0, 144, 210, 0.92), rgba(88, 204, 92, 0.99));
	background: linear-gradient(rgba(0, 144, 210, 0.92), rgba(88, 204, 92, 0.99));
}

.poke__card-info {
	-ms-flex: 1 0 60%;
	    flex: 1 0 60%;
	padding: 30px;
	background-color: rgb(249, 249, 249);
	z-index: 1;
	-webkit-box-shadow: 0px 26px 55px 0px rgba(0, 0, 0, 0.08);
	        box-shadow: 0px 26px 55px 0px rgba(0, 0, 0, 0.08);
	border-radius: 3px;
}

.poke__card-info-title {
	max-width: 270px;
	margin-top: 0;
	margin-bottom: 30px;
	padding-left: 15px;
	border-left: 4px solid #e8e8e8;
	font-size: 27px;
	color: #cecece;
}


.poke__card-title--default {
	font-size: 32px;
	margin: 0 0 15px;
	text-transform: uppercase;
	color: #696969;
}

.poke__card-info-row {
	display: inline-block;
	width: 100%;
	margin-bottom: 15px;
	padding-bottom: 15px;
}

.pokemon__title {
	margin: 0;
	text-transform: uppercase;
	font-size: 16px;
	font-weight: 600;
	color: #a9a9a9;
}

.poke__label {
	display: block;
	font-weight: 700;
	font-size: 14px;
	margin-bottom: 5px;
	color: #585858;
	text-transform: uppercase;
}

/*

Label
Ability
Type
*/

.poke__label-wrapper {
	margin-top: 10px;
}


.poke__label-title {
	margin: 0 8px 0 0;
	display: inline-block;
	font-size: 14px;
	color: #9c9c9c;
	font-weight: 700;
	letter-spacing: .3px;
}

.poke__label-item {
	display: inline-block;
	margin-right: 5px;
	background: #03a9f4;
	border-radius: 3px;
	padding: 5px 9px;
	line-height: 1;
	text-transform: uppercase;
	font-size: 12px;
	font-weight: 700;
	color: #fff;
	letter-spacing: 1px;
}

/*

Not found
*/

.poke__not-found {
	margin-bottom: 60px;
	font-size: 33px;
	color: rgba(142, 142, 142, 0.34);
	text-align: center;
	line-height: normal;
}

.poke__not-found > span:first-child {
    text-transform: capitalize;
}

.poke__not-found > span {
	color: #a5a5a5;
}



